import React from 'react';
import { graphql } from 'gatsby';
import Helmet from 'react-helmet';
import { Header } from '../components';
import { Layout } from '../layouts';
import PropTypes from 'prop-types';
import { PostListItem, PostListWrapper } from '../components';
import { ContentWrapper } from './index';

const ProjectListings = ({ data }) => {
	const { title } = data.site.siteMetadata;
	const coverImage = data.coverImage.childImageSharp.fluid;
	const { nodes } = data.githubData.data;
	const { html } = data.pageDescription;

	return (
		<Layout>
			<Helmet title={`${title} Projects`} />
			<Header title={`${title} Projects`} cover={coverImage} />
			<ContentWrapper>
				<section dangerouslySetInnerHTML={{ __html: html }} />
				<PostListWrapper>
					{nodes.map(node => (
						<PostListItem
							key={node.id}
							cover={node.imageUrl}
							path={node.url}
							title={node.name}
							excerpt={node.description}
							isExternal={true}
							containImgWidth={true}
						/>
					))}
				</PostListWrapper>
			</ContentWrapper>
		</Layout>
	);
};

export default ProjectListings;

ProjectListings.propTypes = {
	data: PropTypes.shape({
		githubData: PropTypes.shape({
			data: PropTypes.shape({
				nodes: PropTypes.arrayOf(
					PropTypes.shape({
						id: PropTypes.string,
						name: PropTypes.string,
						url: PropTypes.string,
						updatedAt: PropTypes.string,
						description: PropTypes.string,
						imageUrl: PropTypes.object,
						stargazers: PropTypes.shape({
							totalCount: PropTypes.number,
						}),
					}).isRequired
				),
			}),
		}),
		site: PropTypes.shape({
			siteMetadata: PropTypes.shape({
				title: PropTypes.string,
			}),
		}),
		coverImage: PropTypes.shape({
			childImageSharp: PropTypes.shape({
				fluid: PropTypes.object,
			}),
		}),
		pageDescription: PropTypes.shape({
			html: PropTypes.string,
		}),
	}),
};

export const query = graphql`
	query {
		githubData {
			data {
				nodes {
					id
					name
					url
					updatedAt
					description
					forkCount
					name
					imageUrl {
						...PostListImageFluid
						extension
						publicURL
					}
					stargazers {
						totalCount
					}
				}
			}
		}
		pageDescription: markdownRemark(
			frontmatter: { type: { eq: "pageDescription" }, page: { eq: "projects" } }
		) {
			html
		}
		coverImage: file(relativePath: { eq: "valley-header.jpg" }) {
			...HeaderImageFluid
		}
		site {
			siteMetadata {
				title
			}
		}
	}
`;
